.homeBody {
  width: 100%;
  height: 100%;
  display: block;
}
.content {
  margin-top: 400px;
  margin-right: auto;
  margin-left: auto;
  width: 60%;
  -ms-flex-item-align: center;
      align-self: center;
  display: block;
}
.buttonGit {
  padding: 0;
  width: 100%;
  height: 75px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.buttonGit button {
  border: none;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  background-color: #000;
  color: #fff;
  font-size: 1.3em;
  border-radius: 5px;
  font-weight: bold;
  font-family: 'Roboto Mono';
  letter-spacing: 3px;
  cursor: pointer;
  padding: 20px;
  position: relative;
  margin-top: 20px;
}



