.navBarContainer {
  display: block;
  width: 100%;
  height: 70px;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  border: 2px solid #000;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.75))), url('../images/portBanner.jpg');
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.75) rgba(0, 0, 0, 0.80)), url('../images/portBanner.jpg');
  background: -o-linear-gradient(rgba(0, 0, 0, 0.75) rgba(0, 0, 0, 0.80)), url('../images/portBanner.jpg');
  background: linear-gradient(rgba(0, 0, 0, 0.75) rgba(0, 0, 0, 0.80)), url('../images/portBanner.jpg');
}
.navBarContainer a {
  color: #fff;
  text-decoration: none;
}

.navBarContainer header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  color: #fff;
  font-family: 'Roboto Mono', monospace;
}

.navBarContainer h1 {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  -ms-flex-item-align: start;
      align-self: flex-start;
  padding-top: 10px;
  font-size: 1em;
}

.navBarContainer header .brand {
  -ms-flex: 4 1 0%;
      flex: 4 1 0%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.navBarContainer header .brand h1 {
  margin: 10px 0 0 25px;
}

.navBarContainer header nav {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.navBarContainer header nav button {
  -ms-flex-item-align: baseline;
      align-self: baseline;
  margin: 10px 25px 0 0;
}

.navBarContainer header nav a {
  text-align: center;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  color: black;
  text-decoration: none;
}

.imageFloat2 {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.imgContainer2 {
  width: 100px;
  margin-left: auto;
  margin-right: auto;
}

.imgContainer2 img {
  width: 100%;
  border-radius: 50%;
  border: solid 6px #000;
}

.menuButton {
  color: #fff;
  background: rgba(0, 0, 0, 0.10);
  border: none;
  width: 50px;
  height: 50px;
  border-radius: 10px;
  font-size: 1.6em;
  margin-right: 25px;
  margin-top: 10px;
  position: fixed;
  right: 0;
  top: 0;
  cursor: pointer;
}
.navClose {
  display: none;
}

.navOpen {
  display: block;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 4;
  background: #000;
  color: #fff;
}
.navContent {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  margin-top: 75px;
}
.navContent li {
  list-style: none;
  padding-top: 20px;
  -ms-flex-item-align: center;
      align-self: center;
}
.navContent li a {
  text-decoration: none;
  color: #fff;
}
@media only screen and (min-width: 600px) {
  .navBarContainer h1 {
    font-size: 1.5em;
    padding-top: 5px;
  }
}

@media only screen and (min-width: 1000px) {
  .navBarContainer h1 {
    font-size: 2em;
    padding-top: 0px;
    padding-left: 30px;
  }
  .navOpen {
    width: 400px;
  }
  .navContent li {
    padding-top: 60px;
  }
}



