.Title {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  height: 350px;
  z-index: 1;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.75))), url('../../images/portBanner.jpg');
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.75) rgba(0, 0, 0, 0.80)), url('../../images/portBanner.jpg');
  background: -o-linear-gradient(rgba(0, 0, 0, 0.75) rgba(0, 0, 0, 0.80)), url('../../images/portBanner.jpg');
  background: linear-gradient(rgba(0, 0, 0, 0.75) rgba(0, 0, 0, 0.80)), url('../../images/portBanner.jpg');
}
.Title .innerTitleDiv {
  width: 80%;
  -ms-flex-item-align: center;
      align-self: center;
}
.Title h1 {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  -ms-flex-item-align: center;
      align-self: center;
  font-size: 1.2em;
  font-weight: bold;
  font-family: 'Roboto Mono', monospace;
  color: #fff;
}
.imgFloat {
  width: 100%;
  position: fixed;
  z-index: 2;
  left: 0;
  top: 270px;
}
.imgContainer {
  width: 120px;
  margin-left: auto;
  margin-right: auto;
  -ms-flex-item-align: center;
      align-self: center;
}
.imgContainer img {
  width: 100%;
  border-radius: 50%;
  border: solid 6px #000;
}

@media only screen and (min-width: 523px) {
  .Title .innerTitleDiv {
    width: 65%;
  }
  .Title h1 {
    font-size: 1.4em;
  }
}

@media only screen and (min-width: 800px) {
  .Title .innerTitleDiv {
    width: 700px;
  }
  .Title h1 {
    font-size: 2em;
    line-height: 1.5em;
  }
}



